/* Android Background: 3D9CDC */
body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: no-repeat fixed center center / cover;
  font-family: 'Yomogi', cursive;
  font-weight: bold;
  font-size:1.1rem;
  overflow:overlay;
}
p {
  margin:0;
}
.cursor-pointer {
  cursor:pointer;
}
table th {
  user-select:none;
}
.hide {
  display:none !important;
}
.break {
  flex-basis: 100%;
  height:0;
}
.no-border {
  border:none !important;
}
.fs-0_7-rem {
  font-size:0.7rem;
}
.fs-0_8-rem {
  font-size:0.8rem;
}
.fs-0_9-rem {
  font-size:0.9rem;
}
.fs-1_0-rem {
  font-size:1rem;
}
.fs-1_1-rem {
  font-size:1.1rem;
}
.fs-1_2-rem {
  font-size:1.2rem;
}
.fs-1_3-rem {
  font-size:1.3rem;
}
.sv-tilde {
  font-family: 'Courier New', monospace;
  /* font-size: 1.2rem; */
  margin-right: 5px;
  color: black !important;
}
.sv-black {
  color:black !important;
}
::-webkit-scrollbar {
  width:4px;
  background:transparent;
  border-radius: 5px;
}
::-webkit-scrollbar-track {
  background-color: transparent;
}
::-webkit-scrollbar-thumb {
  background-color: grey;
  border-radius: 50px;
  background-clip: content-box;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #a8bbbf;
}