.AddItem {
  position:fixed;
  top:0%;
  left:0;
  height:100%;
  width:100%;
  z-index: 6;
}
.AddItem h2 {
  font-size:1.3rem;
  font-weight: bold;
}
.AddItem-clickguard {
  position:absolute;
  top:0;
  left:0;
  width:100%;
  height:100%;
  background: rgba(0,0,0, .4);
}
.AddItem-inner {
  max-height: 86%;
  overflow-y: auto;
  position: absolute;
  top: 10%;
  left:2%;
  padding: 5px 10px 10px 10px;
  background: rgba(255,255,255, .9);
  border-radius: 10px;
  border: 7px ridge;
  border-color: tan;
}
.AddItem input[type="checkbox"] {
  font-size:1rem;
}
.AddItem-close-btn {
  position: absolute;
  top: -20px;
  right: 0px;
  font-size: 3rem;
  cursor:pointer;
}
.AddItem-prices-icon-container {
  position: relative;
  display:inline-block;
  top: 65%;
  color: white;
  font-size: 1rem;
}
.AddItem-prices-star {
  position: absolute;
  top: 45%;
  color: white;
  font-size: .8rem;
}
.AddItem-prices-icon {
  width:45%;
}
.AddItem label {
  margin-right:5px;
}
.AddItem select,
.AddItem input{
  user-select:none;
  font-weight: bold;
  text-transform: capitalize;
  padding:2px;
  border-radius: 3px;
  border:none;
  box-shadow:1px 1px 2px black;
  background:lightblue;
}
.AddItem input[type='text'],
.AddItem input[type='number'],
.AddItem input[type="checkbox"]:not(:checked) {
  background:white;
}
.AddItem-checkbox-label {
  font-size:0.75rem;
}
.AddItem-add-btn {
  background-color: burlywood;
  color: black;
  box-shadow: 2px 2px 3px black;
  border: none;
  border-radius: 5px;
  font-weight: bold;
  font-size: 1.2rem;
  padding: 5px 10px;
  margin-top: 15px;
}
.AddItem-add-btn:active {
  box-shadow: none;
}
.AddItem-perk-container {
  font-size:0.6rem;
}
.AddItem-selected-item-title-container {
  display:flex;
  align-items: center;
}
.AddItem-bottom-container {
  font-size:0.9rem;
  background: rgb(124 192 186 / 80%);
  padding:8px;
  margin-top:15px;
  border-radius: 7px;
}
.AddItem-custom-value-icon {
  margin-right:5px;
}
.AddItem-custom-subtitle {
  margin-left:5px;
  color: darkslategray;
  font-size: .9rem;
}
.AddItem-custom-img {
  width:20px;
  margin-left:5px;
}
.AddItem-greenhouse-img {
  width:25px;
  margin-left:5px;
}